.product-container {
    width: 67%;
    margin-left: auto;
    margin-right: auto;

    .cart-product {
        display: grid;
        grid-template-columns: 1.25fr 1.75fr 0.7fr 1fr;
        margin-bottom: 20px;
    }
}

.product-name {
    font-size: 1.64rem;
    line-height: 110%;
    font-weight: 400;
    margin: 1.0933333333rem 0 0.656rem 0;
}

.hide-on-small {
    display: none;
}

@media only screen and (min-width: 900px) {
    .show-on-small {
        display: none;
    }

    .hide-on-small {
        display: inline-block;
    }
}

.checkout-btn {
    margin-top: 16px;
    width: 45%;
    max-width: 380px;
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;
    background-color: #0a0a0a;
    text-align: center;
    letter-spacing: 0.5px;
    cursor: pointer;
    border: none;
    border-radius: 2px;
    display: inline-block;
    height: 36px;
    line-height: 36px;
    padding: 0 16px;
    text-transform: uppercase;
    vertical-align: middle;
}

$bg: #313841;
$red: #f34541;
$black: #1d242b;
$green: #38b87c;
$blue: #2492ff;

#cart-delete-btn {
    display: flex;
    cursor: pointer;
    border: 0;
    background: transparent;
    outline: 0;
    overflow: hidden;
    .icon {
        position: relative;
        background: $black;
        line-height: 30px;
        width: 30px;
        height: 30px;
        text-align: center;
        color: #fff;
        font-size: 18px;
        transition: 0.2s color;
        border-radius: 2px;
        .fa {
            width: 30px;
            transition: 0.2s all;
        }
        .fa-check {
            color: $green;
        }
        .fa-question {
            color: $blue;
        }
        &:after {
            content: " ";
            display: block;
            position: absolute;
            width: 5px;
            height: 5px;
            transform: rotate(45deg);
            background: $black;
            top: 12.5px;
            right: 1px;
            transition: 0.2s right;
            z-index: 1;
        }
    }
    .text {
        position: relative;
        width: 0;
        height: 30px;
        overflow: hidden;
        font-family: "Roboto", sans-serif;
        background: $red;
        text-align: center;
        line-height: 30px;
        color: #fff;
        font-weight: 300;
        transition: 0.2s all;
        border-top-right-radius: 2px;
        border-bottom-right-radius: 2px;
        span {
            width: 100%;
            opacity: 0;
            position: absolute;
            top: -30px;
            left: 50%;
            transform: translateX(-50%);
            transition: 0.3s all;
        }
    }
    &:hover {
        .icon {
            color: $red;
            border-radius: 0;
            border-top-left-radius: 2px;
            border-bottom-left-radius: 2px;
            &:after {
                right: -2px;
            }
        }
        .text {
            width: 120px;
            span {
                opacity: 1;
                top: 0;
            }
        }
    }
    &.confirm {
        .icon {
            border-radius: 0;
            border-top-left-radius: 2px;
            border-bottom-left-radius: 2px;
            .fa {
                transform: translateY(-30px);
            }
            &:after {
                right: -2px;
            }
        }
        .text {
            background: $blue;
            width: 120px;
            span {
                opacity: 1;
                top: 0;
            }
        }
    }
    &.done {
        .icon {
            border-radius: 0;
            border-top-left-radius: 2px;
            border-bottom-left-radius: 2px;
            .fa {
                transform: translateY(-60px);
            }
            &:after {
                right: -2px;
            }
        }
        .text {
            background: $green;
            width: 120px;
            span {
                opacity: 1;
                top: 0;
            }
        }
    }
}

@keyframes fadeInZoom {
    0% {
        opacity: 0;
        transform: scale(0.7);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}
